@import url(../../node_modules/continuo/dist/css/continuo.css);
dialog {
  position: absolute;
  left: 0;
  right: 0;
  width: -moz-fit-content;
  width: -webkit-fit-content;
  width: fit-content;
  height: -moz-fit-content;
  height: -webkit-fit-content;
  height: fit-content;
  margin: auto;
  border: solid;
  padding: 1em;
  background: white;
  color: black;
  display: block; }

dialog:not([open]) {
  display: none; }

dialog + .backdrop {
  position: fixed;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  background: rgba(0, 0, 0, 0.1); }

._dialog_overlay {
  position: fixed;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0; }

dialog.fixed {
  position: fixed;
  top: 50%;
  -webkit-transform: translate(0, -50%);
  transform: translate(0, -50%); }

.score {
  padding: 5px;
  overflow: hidden;
  min-height: 400px; }

.score_collapsed {
  height: 45px !important; }
  .score_collapsed .score_collapsible {
    display: none; }

.cnt-container {
  position: relative; }

.cnt-emabox {
  position: absolute !important;
  margin-left: 35px !important; }
  .cnt-emabox .cnt-emabox-hide {
    position: absolute;
    left: -20px;
    top: 30px;
    font-size: 17px;
    background-color: #e0e0e0;
    border-radius: 20px;
    padding: 2px 8px 2px 8px; }

/* Make sure to make responsive */
.score_observation_dialog, .score_relationship_dialog, .score_observations_dialog {
  width: 640px; }
  .score_observation_dialog .types, .score_relationship_dialog .types, .score_observations_dialog .types {
    padding: 4px; }
  .score_observation_dialog label, .score_relationship_dialog label, .score_observations_dialog label {
    width: auto; }
  .score_observation_dialog .drop, .score_relationship_dialog .drop, .score_observations_dialog .drop {
    float: right;
    margin-top: -4px; }
  .score_observation_dialog .score_preview_cnt, .score_relationship_dialog .score_preview_cnt, .score_observations_dialog .score_preview_cnt {
    position: absolute;
    background: white;
    top: 10px;
    right: 10px;
    padding: 5px; }
  .score_observation_dialog .score_preview_cnt2, .score_relationship_dialog .score_preview_cnt2, .score_observations_dialog .score_preview_cnt2 {
    position: absolute;
    background: white;
    top: 50px;
    right: 50px;
    padding: 5px; }
  .score_observation_dialog .score_preview_close, .score_relationship_dialog .score_preview_close, .score_observations_dialog .score_preview_close {
    position: absolute !important;
    top: 0;
    right: 0;
    cursor: pointer; }
  .score_observation_dialog i.clickable, .score_relationship_dialog i.clickable, .score_observations_dialog i.clickable {
    cursor: pointer; }

svg .preview_selected {
  color: #ffb944;
  fill: currentColor; }

.truncate {
  width: 160px;
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis; }

.truncate_short {
  width: 112px; }

.observ_ema, .observ_score {
  text-align: right;
  font-size: 20px;
  font-weight: bold;
  width: 450px;
  white-space: nowrap;
  overflow: hidden;
  height: 30px;
  text-overflow: ellipsis; }

.observ_ema {
  color: #448aff; }

.hide_button {
  position: absolute !important;
  top: 10px;
  right: 10px; }

.hide_mode {
  position: absolute !important;
  z-index: 99;
  left: 50%; }

.block {
  display: block; }

.inline {
  display: inline; }

.mask {
  position: absolute;
  z-index: 1001;
  background-color: lightgrey;
  opacity: 0.4; }

.dialog_select {
  margin: 1em 0 1em 0;
  border-top: none;
  border-left: none;
  border-right: none;
  background-color: transparent;
  border-bottom: 3px solid #607d8b;
  min-width: 180px;
  color: rgba(0, 0, 0, 0.54);
  font-size: 16px;
  outline: none; }

.dialog_select:disabled {
  color: rgba(0, 0, 0, 0.26); }

.select_label {
  color: #607d8b;
  font-size: 12px;
  top: 4px; }

.messages {
  display: none;
  background-color: #ffb944 !important;
  color: black;
  padding: 5px; }

.rel_item {
  padding: 0 !important; }

.import {
  width: 640px; }

.addFile {
  width: 640px; }
  .addFile label {
    height: 100% !important; }
  .addFile p {
    max-width: 100% !important; }

#loader {
  display: none;
  margin: 20px 0 0 24px; }
  #loader img {
    width: 50px;
    height: auto; }
